import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Branch } from '../interfaces/branch.model';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icClose from '@iconify/icons-ic/twotone-close';
import icPrint from '@iconify/icons-ic/twotone-print';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPerson from '@iconify/icons-ic/twotone-person';
import icMyLocation from '@iconify/icons-ic/twotone-my-location';
import icLocationCity from '@iconify/icons-ic/twotone-location-city';
import icEditLocation from '@iconify/icons-ic/twotone-edit-location';
import at from '@iconify/icons-fa-solid/at';
import { NgForm, Validators } from '@angular/forms';

import { AddEstablishmentService } from '../../../../shared/services/add_establishment.service'
import { AddEstablishment } from '../../../../shared/models/addEstablishment';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { firestore } from 'firebase';
import { StorageService } from '../../../../shared/services/storage/storage.service';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';

declare var toastr: any;
declare var $: any;
@Component({
  selector: 'vex-branch-create-update',
  templateUrl: './branch-create-update.component.html',
  styleUrls: ['./branch-create-update.component.scss']
})
export class BranchCreateUpdateComponent implements OnInit {

  establecimiento: AddEstablishment = new AddEstablishment();
  
  loading = false;
  establishmentList: AddEstablishment[];
  establishmentObject: AddEstablishment;

  static id = 100;

  form: FormGroup;
  item: any;
  mode: 'create' | 'update' = 'create';

  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icPhone = icPhone;

  constructor(
    @Inject(MAT_DIALOG_DATA) 
    public defaults: any,
    private dialogRef: MatDialogRef<BranchCreateUpdateComponent>,
    private fb: FormBuilder,
    private establishmentService: AddEstablishmentService,
    private firestore: AngularFirestore,
    private storageService: StorageService,
    private router: Router,
    private route: ActivatedRoute,
    ){}

    
  ngOnInit() {

    if(this.storageService.getSession("$key")!=null){
      this.establecimiento = this.storageService.getSession("mydata")     
    }

    if (this.defaults) {
      this.mode = 'update';
    } else {
      this.defaults = {} as AddEstablishment;
    }

    const newId = this.firestore.createId();
    this.storageService.setSession("mykey", newId)

    this.form = this.fb.group({
      
      $key: newId,
      id: [BranchCreateUpdateComponent.id++],     
      nombre: [this.defaults.nombre || ''],         
      tipo: [this.defaults.tipo || ''],
      direccion: this.defaults.direccion || '',
      rfc: this.defaults.rfc || '',
      razonSocial: this.defaults.razonSocial || '',
      telefono: this.defaults.telefono || '',
      email: this.defaults.email || '',
      referencia: this.defaults.referencia || '',


    });

    
  }

  save(establishmentForm: NgForm) {
    if (this.mode === 'create') {
      this.addEstablishment(establishmentForm);
    } else if (this.mode === 'update') {
      this.updateEstablishment();
    }
  }

	addEstablishment(establishmentForm: NgForm) {
    this.establishmentService.postEstablishment(establishmentForm.value);
    this.createBranch();
    alert("Establecimiento guardado")

  }

  updateEstablishment(){
    
    if(this.storageService.getSession("$key")!=null){
      //var value = new AddEstablishment
        var values = { 
          id:  this.establecimiento.id,
          nombre: this.establecimiento.nombre,    
          tipo: this.establecimiento.tipo,
          direccion: this.establecimiento.direccion,
          rfc: this.establecimiento.rfc,
          razonSocial: this.establecimiento.razonSocial,
          telefono: this.establecimiento.telefono,
          email: this.establecimiento.email,
          referencia: this.establecimiento.referencia,
        }

        const branch = this.form.value;
        branch.id = String(values.id)  
        branch.nombre = String(values.nombre)
        branch.tipo = String(values.tipo)
        branch.direccion = String(values.direccion)
        branch.rfc = String(values.rfc)
        branch.razonSocial = String(values.razonSocial)
        branch.telefono = Number(values.telefono)
        branch.email = String(values.email)
        branch.referencia = String(values.referencia)
        

        this.establishmentService.updateEstablishment(values)
        this.updateBranch()
        
        console.log('print value::'+ JSON.stringify(branch));
      }else{
        console.log('no entro')
      }
  }

  

  createBranch() {
    const branch = this.form.value;

    if (!branch.imageSrc) {
      branch.imageSrc = 'assets/img/avatars/1.jpg';
    }

    this.dialogRef.close(branch);
  }

  updateBranch() {
    const branch = this.form.value;
    branch.id = this.defaults.id;

    this.dialogRef.close(branch);
  }

  isCreateMode() {
    return this.mode === 'create';
  }

  isUpdateMode() {
    return this.mode === 'update';
  }
}
